.row{
    margin-left: 10px;
    margin-bottom: 20px;
}
.movie-row{
    display: flex;
    overflow-x: scroll;
    margin-top: 20px;

}
::-webkit-scrollbar{
    display:none;
}
.movie{
    width: 200px;
    height: 200px;
    object-fit: contain;
    transition: transform 460ms;
}
.movie:hover{
    transform: scale(1.06);
}
.movie-poster{
    max-height: 350px;
}